import * as React from 'react';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';

const theme = createTheme({
  palette: {
    primary: {
      main: '#fff',
      light: '#000',
      dark: '#ffffff'
    }
  },
  typography: {
    fontFamily: ['Noto Serif JP', 'YuMincho', 'serif'].join(','),
    h1: {
      fontSize: 40
    },
    h2: {
      fontSize: 32,
      fontWeight: 300,
      letterSpacing: 4
    },
    h3: {
      fontSize: 28,
      fontWeight: 300,
      letterSpacing: 2
    },
    h4: {
      fontSize: 24
    },
    h5: {
      fontSize: 20
    }
  }
});

export const Layout = ({ children }) => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      {children}
    </ThemeProvider>
  );
};
